import { mapState,mapMutations} from 'vuex'
import util from '@/common/util.js'
import config from '@/request/config.js'
export default {
	data() {
		return {
			id:'',
			mask:false,
			text:'222222',
			base64:null,
			relate:'',
			reltype:'',
			image:[],
			canvasHeight:400
		}
	},
	computed:{
		...mapState({
			goodsList:state=>state.goodsList,
			sessionKey:state=>state.sessionKey
		}),
		dataInfo(){
			return this.goodsList.find(item=>item.id==this.id)
		}
	},
	methods: {
		isMask(){
			this.base64=null
			this.mask=false;
			uni.hideLoading();
		},
		getPreive(){
			uni.previewImage({
				current:0,
				urls:this.image
			})
		},
		cuttext(){
			uni.setClipboardData({
				data:this.relate
			})
		},
		share(e){
			let that=this;
			uni.share({
				provider:'weixin',
				scene:'WXSceneSession',
				type:2,
				imageUrl:that.base64,
				success:(res)=>{
					console.log(res)
				},
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		setPoster(){
			let that=this;
			this.mask=true;
			let imgHeight,imgWidth,imageUrl
			uni.showLoading({
				title:'图片生成中...'
			})
			uni.downloadFile({
				url:'https://www.gongsir.club:8081'+this.dataInfo.goodImage,
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				success:(res)=>{
					this.base64=res.tempFilePath
					console.log(this.base64)
					if(res.statusCode===200){
						uni.getImageInfo({
							src:res.tempFilePath,
							success:(res)=>{
								imgHeight=res.height;
								imgWidth=res.width;
								let phoneSize= uni.getSystemInfoSync(),
								phoneWidth=phoneSize.windowWidth,
								phoneHeight=phoneSize.windowHeight,
								text1='发布者:'+this.dataInfo.user.userName,
								text2=this.reltype+':'+this.relate,
								text3='标题:'+this.dataInfo.goodTitle,
								text4='内容:'+this.dataInfo.goodTexts,
								text=[],
								cxt=uni.createCanvasContext('myCanvas')
								text.push(text1);
								text.push(text2);
								text.push(text3);
								text.push(text4);
								util.drawPoster(cxt,this.base64,text,imgHeight,imgWidth)
								cxt.draw(false,()=>{
									let that=this;
									uni.canvasToTempFilePath({
										x:0,
										y:0,
										destWidth:phoneWidth,
										destHeight:phoneHeight,
										canvasId:'myCanvas',
										success:(res)=>{
											that.base64=res.tempFilePath;
											uni.hideLoading()
										}
									})
								});
							}
						})
					}
				}
			})
		},
		savePhone(){
			uni.getSetting({
				success:(res)=>{
					if(!res.authSetting['scope.writePhotosAlbum']){
						uni.authorize({
							scope:'scope.writePhotosAlbum',
							success:(res)=>{
								console.log(res)
							},
							fail:(res)=>{
								uni.showModal({
									title: '温馨提示',
									content: '您已拒绝授权，是否去设置打开？',
									confirmText: "确认",
									cancelText: "取消",
									success:(res)=>{
										if(res.confirm){
											uni.openSetting({
												success() {
													uni.authSetting({
														 "scope.writePhotosAlbum": true
													})
												}
											})
										}
									}
								})
							}
						})
					}else{
						uni.saveImageToPhotosAlbum({
							filePath:this.base64,
							success:(res)=>{
								uni.showToast({
									title:'保存成功'
								})
							}
						})
					}
				}
			})
		},
		toThePublish(openid){//他的发布
			uni.navigateTo({
				url:'../otherPublish/otherPublish?openid='+openid
			})
		}
	},
	onLoad(e){
		this.id=e.id;
		let str=this.dataInfo.relation.split(":");
		this.relate=str[1];
		this.reltype=str[0];
		this.image.push(config.imgUrl+this.dataInfo.goodImage)
	},
	onShareAppMessage(e) {
		this.mask=false;
		if(e.from==='button'){
			console.log(e.target)
		}
		return{
			title:'失物招领信息'
		}
	}
}
